<template>
    <div id="editor">
        <!--<p class="center">代码风格编辑器</p>-->
        <div id="bar">
            <el-button v-for="item in AllBtn"
                :key="item[0]"
                :id="item[0]"
                @click="warp(item)">{{item[1]}}</el-button>
        </div>
        <div id="content"
            contenteditable>{{content}}</div>
        <p class="tips">提示：使用该编辑器，需要自己为代码上色（后期尝试制作代码风格的自动转换）。</p>
        <p class="tips">注：不能使用 TAB 缩进，请使用空格。</p>
        <el-button @click="exchange"
            type="primary">转为html</el-button>
        <div id="ex_content"></div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            AllBtn: [
                ['minititle', '小标题', 'p'],
                ['tip', '提示', 'span'],
                ['p', '段落', 'p'],
                ['code', '代码', 'span'],
                ['code', '前端风格代码块', 'pre'],
                ['cmd', 'cmd风格代码块', 'pre'],
                ['str', 'html字符串', 'span'],
                ['method', 'js内置方法', 'span'],
                ['fun', 'js函数', 'span'],
                ['tag', 'html标签名', 'span'],
                ['word', 'js普通符号、html文字', 'span'],
                ['arrow', 'html箭头', 'span'],
                ['attr', 'html属性', 'span'],
                ['com', '注释', 'span'],
                ['key', 'js关键词、css属性名、颜色', 'span'],
                ['globe', 'js全局变量', 'span'],
                ['spe', 'js特殊符号：$、window、location、href、对象键名', 'span'],
                ['param', 'js变量', 'span'],
                ['flower', '花括号', 'span'],
                ['rect', '方括号', 'span']
            ],
            selected: '',
            content: ''
        };
    },
    methods: {
        warp (item) {
            try {
                let newElement = document.createElement(item[2]);
                newElement.setAttribute('class', item[0]);
                window.getSelection().getRangeAt(0).surroundContents(newElement);
            } catch (error) {
                console.log(error);
                this.$message.warning('本次操作不合规则');
            }
        },
        exchange () {
            // 转换的时候 标签内部的所有内容的 空格 转换为 &nbsp; < 转换成 &lt; > 转换成 &gt; 等等
            // 然后将 剩下的空格清除
        },
        revoke () { }
    }
};
//
//
//        //操作事件添加
//        const $btns =  document.getElementsByClassName('bar_op');
//        for(let i = 0 ; i < $btns.length ; i++ ){
//            $btns[i].addEventListener('click',(event)=>{
//                event.stopPropagation();
//                event.preventDefault();
//                const id = event.target.id === 'code_area' ? 'code' : event.target.id;
//                const type = event.target.getAttribute('type');
//                const cls = id === type ? '' : ' class="' + id +'"';
//                console.log(selected);
//                console.log('<'+type+cls+'>'+selected+'</'+type+'>');
//                $content.innerHTML = $content.innerHTML.replace(selected, '<'+type+cls+'>'+selected+'</'+type+'>');
//            },false);
//        }
//
//        //转换代码
//        const $exchange = document.getElementById('exchange');
//        $exchange.addEventListener('click',()=>{
//            document.getElementById('ex_content').innerHTML = $content.innerHTML.replace(/\</g, '&lt;');
//        },false);
//    }
</script>
<style>
#editor #code_area {
    background: #fdf6e3;
    color: #5a5a5a;
}
#editor #str {
    background: #2aa198;
}
#editor #method {
    background: #7a7a43;
}
#editor #fun {
    background: #b58900;
}
#editor #word {
    background: #586e75;
}
#editor #arrow {
    background: #839496;
}
#editor #attr {
    background: #93a1a1;
}
#editor #com {
    background: #93a1a1;
}
#editor #key {
    background: #859900;
}
#editor #globe {
    background: #660e7a;
}
#editor #spe {
    background: #6c71c4;
}
#editor #flower {
    background: #dc322f;
}
#editor #rect {
    background: #1c8bd2;
}
#editor #cmd {
    background: #444;
}
#editor #p {
    background: #5a5a5a;
}
#editor #param {
    background: #458383;
}
#editor #tag {
    background: #4c8ad1;
}
#editor #code {
    background: #1d8ce0;
}
#editor #minititle {
    background: #2059aa;
}
#editor #tip {
    background: #8492a6;
}
#editor .el-button {
    min-width: 80px;
    color: #fff;
}
#content {
    padding: 30px 200px;
    -webkit-box-shadow: 0 0 10px 0 #eee;
    -moz-box-shadow: 0 0 10px 0 #eee;
    box-shadow: 0 0 10px 0 #eee;
}
#content,
#bar {
    padding: 10px;
}
#bar button {
    height: 30px;
    border: none;
    outline: none;
    padding: 4px 6px;
    margin: 4px;
}
#bar button:hover {
    opacity: 0.7;
}
.tips {
    font-size: 12px;
    color: #999;
}
.center {
    text-align: center;
}
/*代码内容样式*/
#content .minititle {
    padding-left: 4px;
    font-size: 1.1em;
    color: #2059aa;
    text-indent: 0;
}
#content pre {
    font-family: 'arial', sans-serif;
}
#content p {
    font-size: 1em;
    color: #5a5a5a;
    line-height: 1.8em;
}
</style>



